.weather-container {
    width: 100%;
    height: 100%;
    border-left: solid var(--weather-panel-width-border-width) var(--weather-panel-width-border-color);
    border-bottom: solid var(--weather-panel-width-border-width) var(--weather-panel-width-border-color);
}
.weather-attrib {
    background-color: rgb(202, 120, 26);
}
.weather-tiles-container {
    width: 100%;
}

.weather-attrib#temperature {
    width: 100%;
    padding-top: 5%;
    
    text-align: center;
    font-size: 10vw;
}
.weather-attrib#details {
    text-align: center;
    font-size: 2vw;
}

@-webkit-keyframes icon-motion {
    0% {left: 15%;}
    50% {left: 35%;}
    100% {left: 15%;}
}

@keyframes icon-motion {
    0% {left: 15%;}
    50% {left: 35%;}
    100% {left: 15%;}
}
.weather-attrib#icon img {
    position: relative;
    
    -webkit-filter: invert(100%);
    
            filter: invert(100%);
    -webkit-animation-name: icon-motion;
            animation-name: icon-motion;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}
@media screen and (max-width: 1400px) {
    .weather-attrib#temperature {
        font-size: 20vw;
    }
    .weather-attrib#details {
        font-size: 5vw;
    }
    .weather-container {
        border-top: solid var(--weather-panel-width-border-width) var(--weather-panel-width-border-color);
        border-left: none;
        border-bottom: solid var(--weather-panel-width-border-width) var(--weather-panel-width-border-color);
    }
}
@media screen and (min-width: 1600px) {
    .weather-attrib#temperature {
        font-size: 10em;
    }
}
:root {
    --weather-tile-margin: 10px;
    --weather-tile-border-width: 2px;
    --weather-tile-bg-color: hsl(225, 29%, 26%);
    --weather-tile-bd-color: rgb(91, 75, 134);
    --weather-tile-text-color: white;
}

.weather-tile {
    background-color: hsl(225, 29%, 26%);
    background-color: var(--weather-tile-bg-color);
    border: solid 2px rgb(91, 75, 134);
    border: solid var(--weather-tile-border-width) var(--weather-tile-bd-color);

    width: calc(33.33% - 10px * 2 - 2px * 2);

    width: calc(33.33% - var(--weather-tile-margin) * 2 - var(--weather-tile-border-width) * 2);
    height: 100%;
    margin: 10px;
    margin: var(--weather-tile-margin);
    padding-top: 10px;

    color: white;

    color: var(--weather-tile-text-color);

    float: left;
    text-align: center;
}

.weather-tile-date {
    font-weight: bold;
    font-size: 1.15em;
}
.weather-icon-container {
    width: 75%;
    padding-left: calc(25% / 2);
    padding-right: calc(25% / 2);
}
.weather-tile img {
    -webkit-filter: invert(100%);
            filter: invert(100%);
}
.time-container {
    width: 100%;
    height: 100%;
    text-align: center;
}
.display-container {
    width: 100%;
    color: white;
    margin-top: 10vw;
}
.time-display {
    background-color: var(--light-background-color);
    font-size: 15vw;
}
.date-display {
    background-color: var(--highlight-background-color);
    font-size: 5vw;
}
@media screen and (max-width: 1400px) {
    .display-container {
        margin-top: 0;
    }
    .time-display {
        font-size: 20vw;
    }
}
@media screen and (min-width: 1800px) {
    .time-display {
        font-size: 16em;
    }
    .date-display {
        font-size: 4em;
    }
}
:root {
    --menu-line-spacing: 6px;
}

.sbcont {
    position: fixed;
    top: auto;
    bottom: 1vh;
    display: inline-block;
    cursor: pointer;
    margin: 15px;
    padding: 10px;
    background-color: var(--highlight-background-color);
}
.sbcont:hover .bar1 {
    -webkit-transform: translate(15px, 11px) rotateZ(90deg);
            transform: translate(15px, 11px) rotateZ(90deg);
}
.sbcont:hover .bar2 {
    -webkit-transform: rotateZ(90deg);
            transform: rotateZ(90deg);
}
.sbcont:hover .bar3 {
    -webkit-transform: translate(-15px, -11px) rotateZ(90deg);
            transform: translate(-15px, -11px) rotateZ(90deg);
}
.sbcont a {
    margin: 0;
    text-decoration: none;
}

.bar1, .bar2, .bar3 {
    background-color: white;
    width: 35px;
    height: 5px;
    transition: 0.3s;
}
.bar1 {
    margin-top: 0px;
    margin-bottom: 6px;
    margin-bottom: var(--menu-line-spacing);
}
.bar2 {
    margin-top: 6px;
    margin-top: var(--menu-line-spacing);
    margin-bottom: 6px;
    margin-bottom: var(--menu-line-spacing);
}
.bar3 {
    margin-top: 6px;
    margin-top: var(--menu-line-spacing);
    margin-bottom: 0px;
}

@media screen and (max-width: 1400px) {
    .sbcont{
        position: fixed;
        top: 0vh;
        bottom: auto;
    }
}
:root {
    --background-color: #1f1f1f;
    --light-background-color: #333333;
    --highlight-background-color: #3f3f3f;

    --content-height: 80%;
    --settings-height: 20%;

    --time-panel-width: 60%;
    --weather-panel-width: 40%;

    --weather-panel-width-border-color: rgb(153, 91, 50);
    --weather-panel-width-border-width: 5px;
}

html, body, div#root{
    margin: 0;
    background-color: #1f1f1f;
    background-color: var(--background-color);
    height: 100%;
    width: 100%;
    font-family: Arial, Helvetica, sans-serif;
}
html a {
    margin: 0;
    text-decoration: none;
}
/* container for content and settings */
.home {}

.content {
    position: relative;
    height: 80%;
    height: var(--content-height);
    width: 100%;
}
.content-column {
    float: left;
}
.content-column#time {
    width: 60%;
    width: var(--time-panel-width);
}
.content-column#weather {
    width: calc(40% - 5px);
    width: calc(var(--weather-panel-width) - var(--weather-panel-width-border-width));
}
.content:after {
    content: "";
    display: block;
    clear: both;
}
@media screen and (max-width: 1400px) {
    .content-column#time, .content-column#weather{
      width: 100%;
    }
    .content {
        margin-bottom: 50px;
    }
}
.bottom-panel {
    height: 20%;
    height: var(--settings-height);
    width: 100%;
    margin-bottom: 25px;
}
.copyright {
    position: fixed;
    bottom: 0;

    width: 100%;

    background-color: #333333;

    background-color: var(--light-background-color);
    
    color: hsl(0, 0%, 58%);
    font-family: 'Courier New', Courier, monospace;
    font-size: 15px;
    text-align: center;
}
:root {
    --option-button-bg-color: #7777fa;
    --option-button-selected-bg-color: #48cc48;
    --option-label-text-color: white;
}

/* entire container */
.settings-content {}

.options-section {
    color: white;
    color: var(--option-label-text-color);
    width: 80%;
    margin-left: 10%;
}
.settings-content table {
    border-collapse: collapse;
    border: 1px solid white;
}
.settings-content td {
  border: 1px solid #dddddd;
  padding: 20px;
}
.option-button {
    background-color: #7777fa;
    background-color: var(--option-button-bg-color);
    border: none;
    cursor: pointer;
    font-size: 20px;
    padding: 20px;
}
.option-button#selected {
    background-color: #48cc48;
    background-color: var(--option-button-selected-bg-color);
}

.back-button {
    width: auto;
    color: white;
    background-color: red;
    padding: 30px;
}
